.theme-switcher__toggler {
  width: 40px;
  height: 20px;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  padding: 1px;
  transition: background-image .25s linear;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-switcher__toggler:before {
  content: "";
  width: 18px;
  height: 18px;
  filter: drop-shadow(1px 2px 4px #0000001a) drop-shadow(0 2px 2px #0000001a) drop-shadow(1px 1px 2px #0000001a);
  background-image: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: transform .25s linear;
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translateX(0);
}

.theme-switcher__toggler.dark-mode-on {
  background-image: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.theme-switcher__toggler.dark-mode-on:before {
  transform: translateX(110%);
}

